  selectPackageToBookClass(event:any,index:any){
    this.bookingList[index].allUserPackages = event.target.value;
    let pkg = this.bookingList[index].allUserPackages.filter((element) => {
      return element.packageId == event.target.value;
    });
    if(pkg&&pkg.length>0){
      let packageDetail = ''

      if(pkg[0].packageType=='GENERAL'){
        packageDetail =  pkg[0].balance+" Of "+pkg[0].totalCredit+" credits remaining";

      }else{
         packageDetail =  pkg[0].balanceClass+" Of "+pkg[0].totalNumberOfClass+" remaining";

      }
      this.bookingList[index].package = packageDetail;
      this.bookingList[this.selectedUserIndex].packageBalance = pkg.balanceClass;
      this.bookingList[this.selectedUserIndex].selectedPackage =  pkg.packageId;
      this.bookingList[this.selectedUserIndex].paymentId =  pkg.id;

    }
  }
