   getFormatDate(d){
    return d.getMonth()+1 + '/' + d.getDate() + '/' + d.getFullYear()
}

  setUpRequestForSession(type:string,value:any){
    if(type=='range'){
      this.sessionCreateRequest.selectedfromDate = value.split('/')[0];
      this.sessionCreateRequest.selectedtoDate = value.split('/')[1];
      let fromDate = new Date(value.split('/')[0]);
      let toDate = new Date(value.split('/')[1]);
     let minYear = fromDate.getFullYear();
     let maxYear = toDate.getFullYear();
     let minDatePrev = new Date();
     minDatePrev.setDate(minDatePrev.getDate() - 1);
     let minDatePrevFormated  = this.getFormatDate(minDatePrev)
     var minDate = UserSession.checkisPastDate(value.split('/')[0])?this.getFormatDate(new Date()):this.getFormatDate(fromDate);
     var maxDate = this.getFormatDate(toDate)
      let that = this;
      $(function() {
        $('input[name="session"]').daterangepicker({
          singleDatePicker: true,
          showDropdowns: true,
          minYear: minYear,
          maxYear: maxYear,
          minDate: minDate,
          maxDate: maxDate,
          startDate: minDatePrevFormated,
          autoUpdateInput: false,                


        }, function(start, end, label) {
          console.log(start.format('YYYY-MM-DD'), end.format('YYYY-MM-DD'), label);

         
        });
        setTimeout(() => {
          $('input[name="session"]').val('');


        }, 200);
        $('input[name="session"]').on('apply.daterangepicker', function(ev, picker) {
          console.log(picker.startDate.format('YYYY-MM-DD'));
          console.log(picker.endDate.format('YYYY-MM-DD'));
          that.setUpRequestForSession('date',picker.endDate.format('YYYY-MM-DD'));

        });
      }); 
    }
    if(type=='date'){
      if(UserSession.checkIfDateWithinRange(this.sessionCreateRequest.selectedfromDate,this.sessionCreateRequest.selectedtoDate,value)){
        $('input[name="session"]').val(value);

     
      this.sessionCreateRequest.date = value;

      this.sessionCreateRequest.daysId = new Date(value).getDay()+1;

    }else{
      this.globalSubscriptionService.show('400', 'Please select date within range');
    }

    }
    if(type=='time'){
      let fromTime24HourFormat =this.getTwentyFourHourTime(value);

      this.sessionCreateRequest.fromTime = fromTime24HourFormat;

    }
    if(type=='duration'){
      
      let duration=this.convertDurationToMinutes(value);
      let toDate=this.addMinutes(new Date("1/1/2019 "+this.sessionCreateRequest.fromTime),duration);
      let toTimeAMPMFormate=this.formatDateToAMPM(toDate);
      let toTime24HourFormat=this.getTwentyFourHourTime(toTimeAMPMFormate);
      this.sessionCreateRequest.toTime = toTime24HourFormat;


    }
    console.log(this.sessionCreateRequest);
    if(type=='seats'){
      this.sessionCreateRequest.totalSeat = value;

    }
 if(type=='burn_seats'){
  this.sessionCreateRequest.burnSeat = value;

    }
  }
